import React from 'react';

const KindergartenAboutUs = () => (
  <div id='about-us-kindergarten'>
    <div className='container'>
      <h1 className='title'> Fotografia przedszkolna </h1>
    </div>
    <div className='photo-gallery photo-kids'>
      <div className='photo-box'>
        <div className='photos kids2'>
        </div>
      </div>
      <div className='photo-box'>
        <div className='photos kids3'></div>
      </div>
      <div className='photo-box'>
        <div className='photos kids5'></div>
      </div>
    </div>
    <div className='container'>
      <p> Sesje zdjciowe wykonywane w przedszkolach sprawiaj nam duo radoci i satysfakcji.</p>
      <p> Praca z dziemi to najwspanialsza przygoda, ktra moe spotka dorosego czowieka.
        Kady may przedszkolak jest inny, do kadego trzeba podej indywidualnie.
        Najwiksza rado to umiech dziecka po drugiej stronie aparatu: rozluniony, szczliwy, naturalny.</p>
      <p> Sesje zdjciowe w przedszkolach wykonujemy ju od 21 lat.<br />
        Dziaamy na terenie caego lska. </p>
    </div>
    <div className='panorama'></div>
    <footer>Wszelkie prawa zastrzeone</footer>
  </div>
);

export default KindergartenAboutUs;



