import React from 'react';
import '../App.css';

export default class StudioAboutUs extends React.Component {
   
  render() {
    return (
  <div id='about-us-studio'>
    <div className='container'>
      <h1 className='title'> Fotografia studyjna </h1>
    </div>
    <div className='photo-gallery photo-studio'>
      <div className='photo-box'>
        <div className='photos studio08'>
        </div>
      </div>
      <div className='photo-box'>
        <div className='photos bobo'></div>
      </div>
      <div className='photo-box'>
        <div className='photos studio04'></div>
      </div>
    </div>
    <div className='container'>
      <p> Dziaamy od 1997r uwieczniajc na fotografiach najwaniejsze chwile z ycia naszych Klientw.
        Od 21 lat zajmujemy si fotografi rodzinn, biznesow, sesjami przedszkolnymi, lubnymi, komunijnymi.
          Wieloletnie dowiadczenie i indywidualne podejcie do kadego klienta pozwala nam spenia oczekiwania Klientw i czerpa rado z wykonywanej pracy.</p>
    <div className='studio04Small'></div>
    <div className='text-and-photo'> 
    <div className='text-width-photo'>
      <p> To co robimy, to dla nas nie tylko praca - fotografia jest nasz pasj.
        Do kadego klienta podchodzimy indywidualnie, na kad sesj jestemy przygotowani.
        Spenimy Pastwa oczekiwania, zrealizujemy marzenia, podsuniemy propozycje, pokaemy przykadowe aranacje zdj.
          Zapraszamy do odwiedzania naszego profilu na ktrym pojawia si bd aktualne oferty oraz promocje.</p>
      <p> Wsppracujemy z doskonaymi wizaystkami, podczas sesji zadbamy o makija naszych modelek,
          a specjalny kcik fryzjerski z myjk do wosw pozwoli na profesjonalne uoenie wosw.</p>
    </div>
    <div className='flowers'></div>
    </div>
      <p> Zapraszamy do naszego jasnego, klimatycznego studia. Czstujemy kaw, herbat, znajdzie si te co sodkiego ;-) </p>
      <p> Zapraszamy do zapoznania si z nasz ofert.</p>
    </div>
    <div className='panorama'></div>
    <footer>Wszelkie prawa zastrzeone</footer>
  </div>
 )
}
}




